
 /*
  * Component: Button
  * -----------------
  */
  .btn.btn-flat {
    border-radius: 0;
    box-shadow: none;
    border-width: 1px;
  }
  
  .btn.btn-file {
    position: relative;
    overflow: hidden;
  }
  
  .btn.btn-file > input[type='file'] {
    position: absolute;
    top: 0;
    right: 0;
    min-width: 100%;
    min-height: 100%;
    font-size: 100px;
    text-align: right;
    opacity: 0;
    outline: none;
    background: white;
    cursor: inherit;
    display: block;
  }
  
  .btn-default {
    background-color: #f4f4f4;
    color: #444;
    border-color: #ddd;
  }
  
  .btn-default:hover, .btn-default:active, .btn-default.hover {
    color: #2b2b2b;
    background-color: #e7e7e7;
  }
  
  .btn-app {
    border-radius: 3px;
    position: relative;
    padding: 15px 5px;
    margin: 0 0 10px 10px;
    min-width: 80px;
    height: 60px;
    text-align: center;
    color: #666;
    border: 1px solid #ddd;
    background-color: #f4f4f4;
    font-size: 12px;
  }
  
  .btn-app > .fa,
  .btn-app > .fas,
  .btn-app > .far,
  .btn-app > .fab,
  .btn-app > .glyphicon,
  .btn-app > .ion {
    font-size: 20px;
    display: block;
  }
  
  .btn-app:hover {
    background: #f4f4f4;
    color: #444;
    border-color: #aaaaaa;
  }
  
  .btn-app:active, .btn-app:focus {
    box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
  }
  
  .btn-app > .badge {
    position: absolute;
    top: -3px;
    right: -10px;
    font-size: 10px;
    font-weight: 400;
  }